<template>
  <div class="approval-list">
    <van-search v-model="value"
                placeholder="" />
    <div class="filter">
      <van-dropdown-menu>
        <van-dropdown-item v-model="dropMenuValue"
                           :options="option1" />
        <van-dropdown-item v-model="dropMenuValue2"
                           :options="option2" />
      </van-dropdown-menu>
      <div class="num"><span>{{approvalList.length}}</span></div>
    </div>
    <main>

      <van-collapse v-model="activeNames">
        <van-collapse-item :name="approval.id"
                           :key="approval.id + approvalIndex"
                           v-for="(approval, approvalIndex) in approvalList">
          <!--  -->
          <template #title>
            <div>
              <h1>
                <span>{{approval.title}}</span>
                <van-tag :type="approval.approvalStatus |getStatusType">{{approval.approvalStatus |getStatusText}}</van-tag>
              </h1>
              <div><span class="label"></span>{{approval.voucher}}</div>
            </div>
          </template>
          <!--  -->
          <div class="collapse-content">
            <div class="content-item">
              <div class="label"></div>
              <div class="value">{{approval.applicant}}</div>
            </div>
            <div class="content-item">
              <div class="label"></div>
              <div class="value">{{approval.applyTime}}</div>
            </div>
            <div class="content-item">
              <div class="label"></div>
              <div class="value">{{approval.company}}</div>
            </div>
            <div class="content-item">
              <div class="label"></div>
              <div class="value">{{approval.department}}</div>
            </div>
            <div class="content-item">
              <div class="detail" @click="approvalWork(approval)"></div>
            </div>
            <div class="content-item" v-if="approval.applyInstruction!=''">
              <div class="detail" @click="childWorkList(approval)">{{approval.applyInstruction}}</div>
            </div>
          </div>
        </van-collapse-item>
      </van-collapse>
    </main>
  </div>
</template>

<script>
import {myWorkList} from '../api/ssth'

export default {
  name: 'myWorkList',
  data: function () {
    return {
      dropMenuValue: 0, // 
      dropMenuValue2: 0, // 
      option1: [
        { text: '', value: 0 },
        { text: '', value: 1 },
        { text: '', value: 2 }
      ],
      option2: [
        { text: '', value: 0 },
        { text: '', value: 1 },
        { text: '', value: 2 },
        { text: '', value: 3 },
        { text: '', value: 4 }
      ],
      users: {},
      value: '', // 
      approvalList: [
        // {
        //   id: 1,
        //   title: '',
        //   voucher: 'RQ132123123123',
        //   applicant: '1',
        //   applyTime: '2019-07-17',
        //   company: '',
        //   department: '',
        //   applyInstruction: '1111',
        //   approvalStatus: 0
        // }
      ], // 
      activeNames: [], // 
      approval: {}
    }
  },
  activated () {
    this.users = JSON.parse(window.sessionStorage.getItem('users'))
    console.log(this.users)
    myWorkList({userid: this.users.body.userid}).then((data) => {
      console.log(data)
      if (data.status === 0) {
        this.approvalList = data.body
      }
    })
  },
  methods: {
    approvalWork (approval) {
      this.approval = approval
      this.$router.push({name: 'myWork', params: {approval: JSON.stringify(this.approval)}})
    },
    childWorkList (approval) {
      this.approval = approval
      this.$router.push({name: 'childWorkList', params: {approval: JSON.stringify(this.approval)}})
    }
  },
  filters: {
    getStatusType (status) {
      switch (status) {
        case '0':
          return 'warning'
        case '1':
          return 'primary'
        case '2':
          return 'primary'
        case '3':
          return 'warning'
        case '4':
          return 'warning'
        case '5':
          return 'success'
      }
    },
    getStatusText (status) {
      switch (status) {
        case '0':
          return ''
        case '1':
          return ''
        case '2':
          return ''
        case '3':
          return ''
        case '4':
          return ''
        case '5':
          return ''
      }
    }
  }
}
</script>

<style>
.approval-list {
  height: 100%;
  display: flex;
  flex-flow: column;
  background-color: #f4f4f4;
}

.approval-list .filter {
  display: flex;
  align-items: center;
  background-color: #fff;
  box-shadow: 0 2px 12px rgb(100 101 102 / 12%);
}

.approval-list .filter .van-dropdown-menu {
  flex: 1;
}

.approval-list .filter .van-dropdown-menu__bar {
  box-shadow: none;
}

.approval-list .filter .num {
  width: 100px;
  text-align: center;
}

.approval-list main {
  overflow-y: scroll;
}

.approval-list .van-collapse-item {
  margin: 10px 0;
}
.approval-list .van-collapse-item h1 {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  font-size: 14px;
}
.approval-list .van-collapse-item .collapse-content {
  display: flex;
  flex-wrap: wrap;
}

.approval-list .van-collapse-item .collapse-content .content-item {
  width: 50%;
  margin: 10px 0;
}

.approval-list .label {
  font-size: 12px;
  color: #ccc;
}
.approval-list .value {
  color: #000;
}

.approval-list .detail {
  color: rgb(51, 177, 255);
}
</style>



