<template>
  <div style="margin:auto">
    <div style="margin-top: 8rem">
      <div style="width: 20rem;margin: auto">
        <h3 style="text-align:center;">wp</h3>
        <van-form :model='account' style="margin-top: 4rem;">
          <van-cell-group inset>
            <van-field
              v-model="account.username"
              name=""
              label=""
              placeholder=""
              :rules="[{ required: true, message: account.usermessage }]"
            />
            <van-field
              v-model="account.pwd"
              type="password"
              name=""
              label=""
              placeholder=""
              :rules="[{ required: account.pwdrule, message: account.pwdmessage }]"
            />
          </van-cell-group>
          <div style="margin: 16px;">
            <van-button round block type="primary" @click="handleLogin">
              
            </van-button>
          </div>
        </van-form>
      </div>

    </div>
  </div>

</template>

<script>
// ssth.js  
import {requestLogin} from '../api/ssth'
import {Dialog} from 'vant'
export default {
  name: 'Login',
  data () {
    return {
      account: {
        username: '',
        pwd: '',
        usermessage: '',
        pwdmessage: '',
        pwdrule: true
      }
    }
  },
  methods: {
    register () {
      this.$router.push({
        name: 'register',
        params: {
        }
      })
    },
    handleLogin () {
      let loginParams = {name: this.account.username, passwd: this.account.pwd}
      //    
      requestLogin(loginParams).then((data) => {
        if (data.status === 0) {
          sessionStorage.setItem('users', JSON.stringify(data))
          this.$router.push({name: 'hello', params: data})
        } else {
          Dialog.alert({
            message: ''
          }).then(() => {
            // on close
          })
        }
      })
    }
  }
}
</script>

<style>
  body {
    background: #f6f7f9;
  }
</style>



